<template>
  <div>
    <page-header title="Banner 新增" :breadcrumb="breadcrumb" />
    <div class="right_top">
      <!-- 大圖預覽 - Start -->
      <div class="col-sm-12 col-md-8">
        <file-uploader id="uploader_image" @change="onImageChange">
          <label for="uploader_image">
            <img v-if="image" :src="image" width="90%" />
            <div v-else>
              上傳
            </div>
          </label>
        </file-uploader>
      </div>
      <!-- 大圖預覽 - End -->

      <!-- 小圖預覽 - Start -->
      <div class="col-sm-12 col-md-4">
        <file-uploader id="uploader_image_m" @change="onImageMChange">
          <label for="uploader_image_m">
            上傳小圖
            <img :src="imageM" width="90%" />
          </label>
        </file-uploader>
      </div>
      <!-- 小圖預覽 - End -->
    </div>

    <div class="text-center">
      <button class="btn btn-blue" type="button">
        儲存
      </button>
      <router-link to="/banner">
        <button class="btn btn-blue" type="button">
          取消
        </button>
      </router-link>
    </div>
  </div>
</template>

<script>
import PageHeader from '@/components/PageHeader'
import FileUploader from '@/components/FileUploader'

export default {
  data() {
    return {
      breadcrumb: [
        {
          title: 'Banner 內容維護',
          link: '/banner'
        },
        {
          title: '新增'
        }
      ],
      data: {
        title: '',
        description: ''
      },
      image: '',
      imageM: ''
    }
  },
  components: {
    FileUploader,
    PageHeader
  },
  methods: {
    /** 當上傳的圖片改變時，設定顯示圖片路徑 */
    onImageChange({ paths }) {
      this.image = paths.length > 0 ? paths[0] : ''
    },
    /** 當上傳的圖片改變時，設定顯示圖片路徑 */
    onImageMChange({ paths }) {
      this.imageM = paths.length > 0 ? paths[0] : ''
    }
  }
}
</script>
