<template>
  <div>
    <input
      ref="input"
      v-bind="$attrs"
      :id="$attrs.id || 'fileUploader'"
      type="file"
      :class="inputClass"
      :files="value"
      @change="handleChange"
    />
    <slot>
      <label
        :for="$attrs.id || 'fileUploader'"
        class="custom-file-label upload-label"
        data-browse="瀏覽"
      >
        {{ fileName || '選擇檔案' }}
      </label>
    </slot>
  </div>
</template>

<script>
export default {
  inheritAttrs: false,
  props: ['inputClass', 'value'],
  data() {
    return {
      fileName: ''
    }
  },
  methods: {
    handleChange(e) {
      const { files, multiple } = e.target

      this.$emit('input', files)

      if (files.length > 0) {
        this.createFiles(files)

        if (multiple) {
          this.fileName = `已選擇${files.length}份檔案`
        } else {
          this.fileName = files[0].name
        }
      } else {
        this.fileName = ''
        this.$emit('change', { files: null, paths: [] })
      }
    },

    createFiles(files) {
      const paths = []
      const vm = this

      // 多檔處理
      if (files) {
        for (let i = 0; i < files.length; i++) {
          const reader = new FileReader()

          reader.onload = e => {
            paths.push(e.target.result)
            if (i === files.length - 1) {
              vm.$emit('change', { files, paths })
            }
          }

          reader.readAsDataURL(files[i])
        }
      } else {
        vm.$emit('change', { files, paths })
      }
    },

    reset() {
      this.$refs.input.value = ''
      this.handleChange({ target: this.$refs.input })
    }
  }
}
</script>

<style lang="scss" scoped>
.upload-label {
  width: '100%';
  overflow: hidden;
}
</style>
